  .dropdown-menu {
    /* background: red; */
    width: 150px;
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 80px;
    list-style: none;
    text-align: start;
  }
  
  .dropdown-menu li {
    background: #1888ff;
    cursor: pointer;
    
  }
  
  .dropdown-menu li:hover {
    background: #5cabff;
  }
  
  .dropdown-menu.clicked {
    display: none;
  }
  
  .dropdown-link {
    columns: 1;
  
    height: 100%;
    width: 100%;
    text-decoration: none;
    color: #fff;
    padding: 10px;
  }
  
  @media screen and (max-width: 960px) {
    .fa-caret-down {
      display: none;
    }
  }
  